.header {
  padding-top: 15px;
  padding-bottom: 5px;
  display: flex;
}

.headerText {
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
  color: #000000;
}

.profilePanel,
.changePasswordPanel {
  background: #ffffff;
  box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  padding: 15px 17px;
  width: 100%;
}

.profilePhoto {
  width: auto;
  height: auto;
  border-radius: 50%;
  border: 2px solid #093c61;
}

.uploadPhotoButton {
  background: #008662;
  width: 39px;
  height: 39px;
  border: none;
  border-radius: 50%;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 3px;
  box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.15);
}

.inputForm {
  margin-top: 10px;
}

.inputLabel {
  font-size: 14px;
  line-height: 14px;
  padding-bottom: 10px;
  color: #343a40;
}

.inputLabel span {
  color: #ff7878;
  line-height: 14px;
  padding-bottom: 10px;
}

.inputField,
.passwordInputField {
  border: 1px solid #b1b1b1;
  border-radius: 4px;
  font-size: 12px;
  line-height: 14px;
  color: #6d6d73;
  text-indent: 10px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.inputField {
  width: 100%;
}

.passwordInputField {
  width: 90%;
  border-right: none;
}

.inputField::placeholder {
  color: #b1b1b1;
}

.inputField:focus,
.passwordInputField:focus {
  outline: none;
}

.eyeIcon {
  border: 1px solid #b1b1b1;
  border-radius: 4px;
  border-left: none;
  text-align: right;
  padding-right: 15px;
  width: 10%;
}

.eyeIcon img {
  margin-top: 5px;
}

.updateButton,
.resetButton {
  background: #008662;
  border-radius: 4px;
  width: 90px;
  height: 36px;
  border: none;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
}

.resetButton:disabled {
  opacity: 0.8;
}

.changePasswordPanelTitle {
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.03em;
  text-transform: capitalize;
  color: #000000;
}

.successAlert,
.alertSuccessText {
  font-size: 12px;
  line-height: 14px;
  color: #008662;
  margin-top: 15px;
}

.alertErrorText {
  font-size: 12px;
  line-height: 14px;
  color: #ff7878;
  margin-top: 15px;
}

.profilePanelFooter {
  width: 100%;
  text-align: end;
  margin-top: 20px;
}

.changePasswordPanelFooter {
  width: 100%;
  text-align: end;
  margin-top: 20px;
}

.uploadPhotoButton {
  width: 39px;
  height: 39px;
  position: absolute;
  bottom: 0;
  margin-left: 180px;
  margin-bottom: 3px;
  border: none;
  border-radius: 50%;
  background-color: #008662;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
}

.uploadPhotoButton:focus {
  outline: none;
}

.uploadPhotoButton::before {
  content: url('../../assets/icons/Cloud-Upload.svg');
  line-height: 44px;
  margin-left: 1.5px;
}

.alertText {
  font-size: 11px;
  color: #ff7878;
}

.userPhoto {
  height: 110px;
  width: 110px;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
  border: 3px solid #c7bdbd;
}
